// import axios from 'axios'
import md5 from 'md5'
export const getCharacterByBane = async (name: string) => {
  const timeStamp = new Date().getTime()
  const hash = md5(
    String(timeStamp + process.env.NEXT_PUBLIC_MARVEL_PRIVATE_KEY! + process.env.NEXT_PUBLIC_MARVEL_PUBLIC_KEY!)
  )
  console.log(hash)
  const url = `https://gateway.marvel.com:443/v1/public/characters?nameStartsWith=${name}&apikey=${process.env.NEXT_PUBLIC_MARVEL_PUBLIC_KEY}&hash=${hash}&ts=${timeStamp}&limit=100`
  // const res = await fetch(url)
  // const data = res.json()

  // return data
  const res = await fetch(url)
  const result = await res.json()
 const {data} = result
 return data.results
}
